import { Injectable } from '@angular/core';
import {Http, Headers, RequestOptions} from '@angular/http';
import {tokenNotExpired} from 'angular2-jwt';
import 'rxjs/add/operator/map'

@Injectable()
export class AuthService {
  authToken: any;
  user: any;

  constructor(private http:Http ) { }

  registerUser(user){
    let headers = new Headers({
      'Content-Type': 'application/json'
    });
    let options = new RequestOptions({headers:headers})
    return this.http.post('http://localhost:3000/users/register', user, options)
      .map(res => res.json());
  }

  authUser(user){
    let headers = new Headers({
      'Content-Type': 'application/json'
    });
    let options = new RequestOptions({headers:headers})
    return this.http.post('http://localhost:3000/users/authenticate', user, options)
    .map(res => res.json());
  }

  getProfile(){
    this.loadToken();
    let headers = new Headers({
      'Content-Type': 'application/json',
      'Authorization': this.authToken
    });
    let options = new RequestOptions({headers:headers})
    return this.http.get('http://localhost:3000/users/profile', options)
    .map(res => res.json());
  }

  loadToken(){
    const token = localStorage.getItem('id_token');
    this.authToken = token;
  }

  loggedIn(){
    return tokenNotExpired('id_token');
  }

  storeUserData(token, user){
    localStorage.setItem('id_token', token);
    localStorage.setItem('user', JSON.stringify(user));
    this.authToken = token;
    this.user = user;
  }

  logout(){
    this.authToken = null;
    this.user = null;
    localStorage.clear();
  }

}



