import { Component, OnInit } from '@angular/core';
import {ValidateService} from '../../services/validate.service';
import {FlashMessagesService} from 'angular2-flash-messages';
import {AuthService} from '../../services/auth.service';
import {Router} from '@angular/router';

@Component({
  selector: 'app-register',
  templateUrl: './register.component.html',
  styleUrls: ['./register.component.css']
})
export class RegisterComponent implements OnInit {
  name: string;
  username: string;
  email: string;
  password: string;

  constructor(
    private validate: ValidateService, 
    private flash:FlashMessagesService, 
    private auth: AuthService,
    private router: Router
  ) { }

  ngOnInit() {
  }

  onRegister(){
    const user = {
      name: this.name,
      email: this.email,
      username: this.username,
      password: this.password
    }

    if(!this.validate.validateRegister(user)){
      this.flash.show('Please enter all the required information', {cssClass: 'alert-danger', timeout:3500});
      return false;
    }

    if(!this.validate.validateEmail(user.email)){
      this.flash.show('Please enter a valid email address', {cssClass: 'alert-danger', timeout:3500});
      return false;
    }

    this.auth.registerUser(user).subscribe(data => {
      if(data.success){
        this.flash.show('You have been registered! Please log in', {cssClass: 'alert-success', timeout:3000});
        this.router.navigate(['/login']);
        
      }else {
        this.flash.show('There is an error with your registration, please try again', {cssClass: 'alert-danger', timeout:3000});
        this.router.navigate(['/register']);
      }
    })


  }

}



